'use client';
import { motion, useScroll, useTransform, AnimatePresence } from 'framer-motion';
import { useEffect, useState, useRef } from 'react';

import { FiCheckCircle } from 'react-icons/fi';

const heroColors = ['bg-blue-900', 'bg-emerald-800', 'bg-amber-700', 'bg-slate-800', 'bg-rose-900'];

export default function Home() {
  const [currentSlide, setCurrentSlide] = useState(0);
  const projectsRef = useRef(null);
  const { scrollYProgress } = useScroll({ target: projectsRef, offset: ["start end", "end start"] });
  const rotate = useTransform(scrollYProgress, [0, 1], [0, 15]);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % heroColors.length);
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="overflow-hidden">
      {/* Hero Section - Enhanced transitions */}
      <section className="relative h-screen">
        <AnimatePresence mode='wait'>
          {heroColors.map((color, index) => (
            currentSlide === index && (
              <motion.div
                key={index}
                initial={{ opacity: 0, scale: 1.1 }}
                animate={{ opacity: 1, scale: 1 }}
                exit={{ opacity: 0, scale: 0.9 }}
                transition={{ duration: 1.2, ease: [0.4, 0, 0.2, 1] }}
                className={`absolute inset-0 ${color} flex items-center justify-center`}
              >
                <div className="container text-center text-white px-4">
                  <motion.h1
                    initial={{ y: 40, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ duration: 0.8 }}
                    className="text-5xl md:text-7xl font-bold mb-6 font-serif"
                  >
                    Engineering Excellence
                  </motion.h1>
                  <motion.p
                    initial={{ y: 30, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ delay: 0.3, duration: 0.8 }}
                    className="text-xl md:text-3xl mb-12 max-w-2xl mx-auto"
                  >
                    Transforming Africa&apos;s Infrastructure Landscape
                  </motion.p>
                  <motion.div
                    initial={{ scale: 0, opacity: 0 }}
                    animate={{ scale: 1, opacity: 1 }}
                    transition={{ delay: 0.6, type: 'spring' }}
                  >
                    <button className="bg-white/10 backdrop-blur-lg text-white px-10 py-4 rounded-full hover:bg-white/20 transition-all border-2 border-white/20 font-medium text-lg">
                      Explore Our Projects
                    </button>
                  </motion.div>
                </div>
              </motion.div>
            )
          ))}
        </AnimatePresence>

        {/* Carousel Dots */}
        <div className="absolute bottom-8 left-1/2 -translate-x-1/2 flex space-x-3">
          {heroColors.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentSlide(index)}
              className={`w-3 h-3 rounded-full transition-all duration-300 ${
                index === currentSlide ? 'bg-white scale-125' : 'bg-white/30 scale-100'
              }`}
            />
          ))}
        </div>
      </section>

      {/* Stats Banner - Enhanced with icons */}
      <motion.section 
        initial={{ opacity: 0, y: 40 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true, margin: "-100px" }}
        transition={{ duration: 0.6 }}
        className="bg-gradient-to-r from-blue-900 to-emerald-900 text-white py-24"
      >
        <div className="container grid md:grid-cols-3 gap-12 text-center">
          <motion.div 
            whileHover={{ scale: 1.05 }}
            className="p-8 rounded-xl bg-white/5 backdrop-blur-sm hover:bg-white/10 transition-all">
            <FiCheckCircle className="text-4xl mx-auto mb-4" />
            <div className="text-5xl font-bold mb-2">250+</div>
            <div className="text-sm uppercase tracking-widest">Projects Completed</div>
          </motion.div>
          {/* Repeat similar structure for other stats */}
        </div>
      </motion.section>

      {/* Services Section - Animated Grid */}
      <section className="py-28 bg-gradient-to-b from-slate-50 to-white">
        <div className="container">
          <motion.h2 
            initial={{ opacity: 0, y: 40 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
            className="text-5xl font-bold text-center mb-20 font-serif"
          >
            Our Core Services
          </motion.h2>
          
          <motion.div 
            className="grid md:grid-cols-2 lg:grid-cols-3 gap-8"
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, margin: "-100px" }}
            variants={{
              visible: { transition: { staggerChildren: 0.1 } }
            }}
          >
            {SERVICES.map((service, index) => (
              <ServiceCard 
                key={index}
                title={service.title}
                description={service.description}
                index={index}
              />
            ))}
          </motion.div>
        </div>
      </section>

      {/* Featured Projects - Parallax Effect */}
      <section ref={projectsRef} className="py-28 bg-slate-50 relative overflow-hidden">
        <motion.div 
          style={{ rotate }}
          className="absolute -right-48 -top-48 w-96 h-96 bg-gradient-to-r from-blue-400 to-emerald-400 rounded-full opacity-10"
        />
        
        <div className="container relative">
          <motion.h2 
            initial={{ opacity: 0, x: -40 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            className="text-5xl font-bold text-center mb-20 font-serif"
          >
            Recent Success Stories
          </motion.h2>
          
          <div className="grid md:grid-cols-2 gap-12">
            {PROJECTS.map((project, index) => (
              <ProjectCard
                key={index}
                project={project}
                index={index}
              />
            ))}
          </div>
          
          <motion.div 
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            viewport={{ once: true }}
            className="mt-20 text-center"
          >
            <button className="bg-gradient-to-r from-blue-600 to-emerald-600 text-white px-16 py-5 rounded-xl hover:scale-105 transition-transform font-medium text-lg shadow-lg">
              View All Projects
            </button>
          </motion.div>
        </div>
      </section>
    </div>
  );
}